/* src/app/components/stepper/stepper.css */
.form {
  display: flex;
  flex-direction: column;
  align-items: start;
}
.form-body {
  display: flex;
  justify-content: space-evenly;
  align-items: start;
}
.container {
  display: flex;
}
.resume-cards {
  display: flex;
  flex-direction: column;
}
.stepper {
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  max-width: 90%;
  background: white;
}
.stepper-footer {
  display: flex;
  justify-content: end;
  align-items: center;
  background-color: rgba(186, 205, 244, 0.116);
  border-radius: 5px;
}
.no-radius {
  border-radius: 0px;
}
.fomr-tittle-section {
  margin-left: auto;
  margin-right: auto;
  font-weight: 500;
  color: #006192;
}
.form-tittle {
  font-weight: 500;
  color: #006192;
}
.application-card {
  color: #006192;
  background-color: rgba(246, 246, 246, 0.429);
  height: 400px;
  padding: 10px;
  margin-bottom: 10px;
}
